<template>
  <div class="app-container">
    <div class="filter-container">
      <el-date-picker
        v-model="date"
        type="date"
        class="filter-item"
        size="small"
        placeholder="请选择日期"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        size="small"
        icon="el-icon-search"
        @click="handleFilter"
      >搜索</el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      stripe
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="序号" width="75">
        <template slot-scope="scope">{{ scope.$index }}</template>
      </el-table-column>
      <el-table-column label="日期" align="center">
        <template
          slot-scope="scope"
        >{{ scope.row.ctime | timestampFilter("{y}-{m}-{d} {h}:{i}:{s}") }}</template>
      </el-table-column>
      <el-table-column label="IP" align="center">
        <template slot-scope="scope">{{ scope.row.ip }}</template>
      </el-table-column>
      <el-table-column label="地址" align="center">
        <template slot-scope="scope">{{ scope.row.url }}</template>
      </el-table-column>
      <el-table-column label="耗时（毫秒）" align="center">
        <template slot-scope="scope">{{ scope.row.cost }}</template>
      </el-table-column>
      <!--      <el-table-column type="expand" label="详情" width="50">-->
      <!--        <template slot-scope="props">-->
      <!--          <el-form label-position="left" inline>-->
      <!--            <el-form-item label="请求">-->
      <!--              <pre>{{ decode(props.row.request) }}</pre>-->
      <!--            </el-form-item>-->
      <!--            <el-form-item label="返回">-->
      <!--              <pre>{{ decode(props.row.response) }}</pre>-->
      <!--            </el-form-item>-->
      <!--          </el-form>-->
      <!--        </template>-->
      <!--      </el-table-column>-->
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
  </div>
</template>

<script>
import { requestList } from "@/api/record";
import { parseTime } from "@/utils";
import waves from "@/directive/waves"; // waves directive
import Pagination from "@/components/Pagination";
let Base64 = require("js-base64").Base64;

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    timestampFilter(time, fm) {
      return parseTime(time, fm);
    }
  },
  data() {
    return {
      total: 0,
      listQuery: {
        dateStr: "",
        pageNo: 1,
        pageSize: 20
      },
      date: new Date(),
      list: null,
      listLoading: true
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      this.listQuery.dateStr = parseTime(this.date, "{y}-{m}-{d}");
      requestList(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
          this.listLoading = false;
        }
      });
    },
    handleFilter() {
      this.listQuery.pageNo = 1;
      this.fetchData();
    },
    decode(data) {
      let decode = Base64.decode(data);
      try {
        return JSON.parse(decode);
      } catch (e) {
        console.log(decode);
      }
    }
  }
};
</script>
